import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId,  fetchPostH, getUrlServerSide } from "../../../stores/Utils";

import {  TextInput_Cmpt } from "../../../components";

import "../component/dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";
import { DateCalComponent_Cmpt } from "../../Inventory";


const TextInput = TextInput_Cmpt();
const DateCalComponent = DateCalComponent_Cmpt();



const frmNm = "upd_passport"

const UpdPassportRecord  = ({ data, confirm, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const print  = async ()  => {
		let ffm = getStore(frmNm);
		confirm && confirm(ffm);
		close();
	};





	const close  = ()  => {
		data?.modalID && CloseModal({id:data?.modalID})
	}


	
	

	return (
		<div style={{
			maxHeight: "80vh",
			width: "690px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Actualizar Entrada
				</p>
				<div className="flexSpace"/>
				
			</div>
			
			<div className="_dsplFlx spaceAround flxWrp">
				
				<div className="flexSpace"/>
				<div style={{margin:"10px 10px 0 0"}}>
					<DateCalComponent  
						init={getStore(frmNm)?.["dateOfPassport"]} 
						label={"Entrada a Usa"}
						updDate={(e)=>updFrm("dateOfPassport",e)}
					/>
				</div>
				<div className="flexSpace"/>
				<div style={{margin:"10px 10px 0 0"}}>
					<DateCalComponent  
						init={getStore(frmNm)?.["dateOfLeft"]} 
						label={"Salida de Usa"}
						updDate={(e)=>updFrm("dateOfLeft",e)}
					/>
				</div>
				<div className="flexSpace"/>
				<div style={{margin:"10px 10px 0 0"}}>
					<DateCalComponent  
						init={getStore(frmNm)?.["lastLeftYourCountry"]} 
						label={"Salida de su pais"}
						updDate={(e)=>updFrm("lastLeftYourCountry",e)}
					/>
				</div>
				<div className="flexSpace"/>
				
				
			</div>
			<div className="_dsplFlx spaceAround flxWrp " style={{marginTop:30}}> 
				
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["placeOfPassport"]} 
					label={"Puerto de Entrada"} 
					width={200} 
					updChanges={(e)=>updFrm("placeOfPassport",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["status"]} 
					label={"Estatus"} 
					width={200} 
					updChanges={(e)=>updFrm("status",e)}
				/>
				<div className="flexSpace"/>
			</div>

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<div className="card">
					<button onClick={print}>
						Guardar
					</button>
				</div>

			</div>
		

			
		</div>
	);
};

export default UpdPassportRecord;
