import React, { useEffect, useState, useRef } from "react";



import { getStore, gen16CodeId, ObjectKeys,  parseDate, updStore, _monthNames, copyToClipboard, parseDateMMDDYYYY, fetchPostH, getUrlServerSide } from "../../../stores/Utils";



import "../component/dashboard.css"
import { OpenModal } from "../../../components/DialogHRM";
import AddPassportRecord from "./PassportRecordAdd";
import { SvgIcon_Cmpt } from "../../../components";
import UpdPassportRecord from "./PassportRecordUpdate";
import DeleteAlert from "../component/deleteAlert";



const Svg = SvgIcon_Cmpt();



const PassportRecordDashboard  = ({ openUpd,   close }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [view, set_view] = useState(1);


	const updObs =  ()=>{
		set_obs(gen16CodeId())
	}
	

	const addPassportRecord = async (v)=>{
		let ffm = {...getStore("upd_client")};

		let passportRecord = ffm?.passportRecord || [];
		passportRecord.push(v)
		

		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {passportRecord }
		};

		// console.log(bdyq2)
		
		/*  */
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			ffm.passportRecord = passportRecord;
			updStore("upd_client", ffm);
			updStore("new_passport", null);
			updObs();
		}
		
	}


	
	const updPassportRecord = async (v, indX2)=>{
		let ffm = {...getStore("upd_client")};

		let passportRecord = ffm?.passportRecord || [];
		passportRecord[indX2] = v;


		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {passportRecord }
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			ffm.passportRecord = passportRecord;
			updStore("upd_client", ffm);
			updStore("upd_passport", null);
			updObs()
		}
		
	}

	const rmvPassportRecord = async (indX2)=>{
		let ffm = {...getStore("upd_client")};

		let passportRecord = ffm?.passportRecord || [];
		passportRecord.splice(indX2,1);


		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: {passportRecord }
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			ffm.passportRecord = passportRecord;
			updStore("upd_client", ffm);
			updObs()
		}
		
	}


	const openUpdView =  () => {

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<AddPassportRecord confirm={addPassportRecord} />
		OpenModal(modal_data);
	}

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			// let user = {...getStore("upd_client")};
			set_obs(gen16CodeId())
		}
	});





	let list = getStore("upd_client")?.passportRecord || [];

	return (
		<div className=" ">
		
			<div className={"detailClient"}>
				<div className="_dsplFlx ">
					<p className="form_title">
						Pasaportes
					</p>
					<div className="flexSpace"/>
					<p className="group_title addBtn" onClick={openUpdView}>
						Agregar Pasaporte
					</p>
				</div>
			</div>
				
			<div className=" ">
				{list.map((item2, indX2)=> 
					<PassportRecordItem  
						residence={item2} 
						key={indX2}
						index={indX2}
						updPassportRecord={updPassportRecord}
						deleteJob={rmvPassportRecord}
					/>
				)}
			</div>
		</div>
	);
};

export default PassportRecordDashboard;







const PassportRecordItem  = ({ residence,   updPassportRecord, index, deleteJob }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const updObs  =  (v)  => {
		set_obs( gen16CodeId());
	};

	const copyToClpb  =  (v)  => {
		copyToClipboard( v,updObs);
	};

	const _updPassportRecord  =  (v)  => {
		updPassportRecord(v,index)
	};

	const openUpdView =  () => {
		updStore("upd_entry_usa", residence);
		updStore("upd_entry_usa_bck", JSON.parse(JSON.stringify(residence)));
		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<UpdPassportRecord confirm={_updPassportRecord} />
		OpenModal(modal_data);
	}

	const confirmDelete =  (v)  => {
		deleteJob(index)
	};


	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert 
				confirm={confirmDelete}
				lbl_p={"Eliminar Pasaporte"} 
				lbl_span={"Una vez eliminado no podra ser recuperado"}
			/>
			OpenModal(modaldata);
		}
	}


	return (

		<div className="_dsplFlx ">
			<div className="flexSpace"/>
			<div className={`card_address modern `}  >
				<div className="_dsplFlx ">
					<div className="title_card" onClick={()=>copyToClpb(residence.passportNumber)}>
						{residence.passportNumber}
					</div>
					<div className="flexSpace"/>
					<div className={"icon_action _dsplFlx"}>
						<div className={"menu_icon _dsplFlx"}> 
							<div className="flexSpace"/>
							<Svg 
							name={"more_vert"}	/>
						</div>
						<div className={"menu_icon_acc _dsplFlx"}>
							<div 	onClick={openUpdView}>
								<Svg name={"outline_edit"}	/>
							</div>
							<div className="flexSpace"/>
							<div 	onClick={OpenDeleteAlert}>
								<Svg name={"outline_delete"}	/>
							</div>
						</div>
					</div>
				</div>
				<div className="body_card">
					<div className="_dsplFlx city_state">
						<div className="h4_gsans"  onClick={()=>copyToClpb(residence.countryOfIssuance)}>
							{residence.countryOfIssuance}
						</div>
					</div>
				</div>
				<div className="" style={{marginTop:15}}>
					
					{residence.expirationDate?
					<div className="_dsplFlx city_state">
						<div className="h5_gsans" >
							{"Expira:  "}
						</div>
						
						<div className="h5_gsans"  onClick={()=>copyToClpb(parseDateMMDDYYYY(residence.expirationDate))}>
							{parseDate(residence.expirationDate)}
						</div>
						
					</div>
					:null}
					{residence.issueDate?
					<div className="_dsplFlx city_state">
						<div className="h5_gsans" >
							{"Emitido:  "}
						</div>
						<div className="h5_gsans"  onClick={()=>copyToClpb(parseDateMMDDYYYY(residence.issueDate))}>
							{parseDate(residence.issueDate)}
						</div>
					</div>
					:null}
					
				</div>
			</div>
			<div className="flexSpace"/>
		</div>
	)
}











const compareJSON = (fls,obj, bkUp) =>{
	let r = 0;
	let data = {}
	for(let i in fls){
		let k1 = fls[i];
		if(obj[k1] ){
			if(typeof obj[k1] === "object"){
				ObjectKeys(obj[k1]).map(fk2=>{
					if(obj?.[k1]?.[fk2]!==bkUp?.[k1]?.[fk2]){
						r = 1;
						data[k1] = obj[k1];
					}
				})
			}
			else if(obj[k1] &&  obj[k1]!==bkUp[k1]){
				r = 1;
				data[k1] = obj[k1];
			}
		}
	}
	return {data, hasChange:r};
  }



